
"use client";

import * as React from "react";
import { useForm } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import * as z from "zod";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Dialog, DialogContent, DialogDescription, DialogFooter, DialogHeader, DialogTitle } from "@/components/ui/dialog";
import { Form, FormControl, FormField, FormItem, FormLabel, FormMessage } from "@/components/ui/form";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import type { User, Role } from "@/types";
import { ROLES } from "@/lib/constants";
import { Loader2 } from "lucide-react";

const formSchema = z.object({
  email: z.string().email({ message: "Alamat email tidak valid." }).optional(), // Email might not be editable here
  password: z.string().min(6, { message: "Kata sandi minimal 6 karakter." }).optional(),
  role: z.enum(['admin', 'guru', 'siswa', 'pimpinan'], { required_error: "Peran dibutuhkan." }).optional(), // Role might be changed elsewhere
  name: z.string().min(2, { message: "Nama minimal 2 karakter."}).optional(),
});

type UserFormProps = {
  isOpen: boolean;
  onClose: () => void;
  onSubmit: (data: z.infer<typeof formSchema>, editingUser: User | null) => Promise<void>;
  editingUser: User | null;
  isLoading?: boolean;
};

export function UserForm({ isOpen, onClose, onSubmit, editingUser, isLoading }: UserFormProps) {
  const form = useForm<z.infer<typeof formSchema>>({
    resolver: zodResolver(formSchema),
    defaultValues: {
      email: "",
      password: "",
      role: "siswa",
      name: "",
    },
  });

  React.useEffect(() => {
    if (editingUser) {
      form.reset({
        email: editingUser.email || "", // Display email if available
        role: editingUser.role as 'admin' | 'guru' | 'siswa' | 'pimpinan',
        password: "", 
        name: editingUser.name || "",
      });
    } else {
      // For creation, some fields might be handled by registration process
      form.reset({ email: "", password: "", role: "siswa", name: "" });
    }
  }, [editingUser, form, isOpen]);

  const handleSubmit = async (values: z.infer<typeof formSchema>) => {
    const submitValues: Partial<z.infer<typeof formSchema>> = { name: values.name };
    if (values.password) { // Only include password if provided
      submitValues.password = values.password;
    }
    // Email and role changes are typically handled by specific functions, not this generic form submit
    // unless the onSubmit prop is designed for it.
    await onSubmit(submitValues as z.infer<typeof formSchema>, editingUser);
  };

  return (
    <Dialog open={isOpen} onOpenChange={onClose}>
      <DialogContent className="sm:max-w-[425px]">
        <DialogHeader>
          <DialogTitle>{editingUser ? "Edit Pengguna" : "Buat Pengguna Baru"}</DialogTitle>
          <DialogDescription>
            {editingUser ? "Perbarui detail pengguna." : "Isi detail untuk membuat akun pengguna baru."}
          </DialogDescription>
        </DialogHeader>
        <Form {...form}>
          <form onSubmit={form.handleSubmit(handleSubmit)} className="space-y-4 py-4">
            <FormField
              control={form.control}
              name="name"
              render={({ field }) => (
                <FormItem>
                  <FormLabel>Nama Lengkap</FormLabel>
                  <FormControl>
                    <Input placeholder="John Doe" {...field} />
                  </FormControl>
                  <FormMessage />
                </FormItem>
              )}
            />
            {editingUser && editingUser.email && ( // Only show email field if editing and email exists
                 <FormField
                    control={form.control}
                    name="email"
                    render={({ field }) => (
                        <FormItem>
                        <FormLabel>Email</FormLabel>
                        <FormControl>
                            <Input placeholder="pengguna@contoh.com" {...field} disabled={true} />
                        </FormControl>
                        <p className="text-xs text-muted-foreground">Email tidak dapat diubah.</p>
                        <FormMessage />
                        </FormItem>
                    )}
                />
            )}
            <FormField
              control={form.control}
              name="password"
              render={({ field }) => (
                <FormItem>
                  <FormLabel>{editingUser ? "Kata Sandi Baru (opsional)" : "Kata Sandi"}</FormLabel>
                  <FormControl>
                    <Input type="password" placeholder="••••••••" {...field} />
                  </FormControl>
                   {editingUser && <p className="text-xs text-muted-foreground">Kosongkan jika tidak ingin mengubah kata sandi.</p>}
                  <FormMessage />
                </FormItem>
              )}
            />
            {/* Role is generally changed via UserTableActions, not directly in this form for existing users */}
            {!editingUser && (
                 <FormField
                    control={form.control}
                    name="role"
                    render={({ field }) => (
                        <FormItem>
                        <FormLabel>Peran</FormLabel>
                        <Select 
                            onValueChange={field.onChange} 
                            defaultValue={field.value} 
                            value={field.value} 
                            disabled={!!editingUser} // Disable role change in form if editing
                        >
                            <FormControl>
                            <SelectTrigger>
                                <SelectValue placeholder="Pilih peran" />
                            </SelectTrigger>
                            </FormControl>
                            <SelectContent>
                            {Object.entries(ROLES)
                                .filter(([roleKey]) => roleKey !== 'superadmin')
                                .map(([roleKey, roleName]) => (
                                <SelectItem key={roleKey} value={roleKey}>
                                {roleName}
                                </SelectItem>
                            ))}
                            </SelectContent>
                        </Select>
                        {editingUser && <p className="text-xs text-muted-foreground">Peran diubah melalui menu tindakan tabel.</p>}
                        <FormMessage />
                        </FormItem>
                    )}
                />
            )}
            <DialogFooter>
              <Button type="button" variant="outline" onClick={onClose} disabled={isLoading}>Batal</Button>
              <Button type="submit" disabled={isLoading}>
                {isLoading && <Loader2 className="mr-2 h-4 w-4 animate-spin" />}
                {editingUser ? "Simpan Perubahan" : "Buat Pengguna"}
              </Button>
            </DialogFooter>
          </form>
        </Form>
      </DialogContent>
    </Dialog>
  );
}
