
"use client";

import React, { useState, useEffect, useCallback } from "react";
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card";
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import { useAuth } from "@/hooks/use-auth";
import type { User, Role } from "@/types";
import { ROLES } from "@/lib/constants";
import { UserPlus, CheckCircle, ShieldAlert, Loader2 } from "lucide-react";
import { UserForm } from "@/components/admin/user-form";
import { UserTableActions } from "@/components/admin/user-table-actions";
import { useToast } from "@/hooks/use-toast";
import { supabase } from "@/lib/supabaseClient"; // Assuming supabaseClient is set up

export default function AdminUsersPage() {
  const { session, isLoading: authLoading } = useAuth();
  const { toast } = useToast();

  const [users, setUsers] = useState<User[]>([]);
  const [isFormOpen, setIsFormOpen] = useState(false);
  const [editingUser, setEditingUser] = useState<User | null>(null);
  const [pageLoading, setPageLoading] = useState(false);
  const currentUser = session?.user as User | undefined;

  const fetchUsers = useCallback(async () => {
    setPageLoading(true);
    const { data, error } = await supabase
      .from('profiles')
      .select('id, name, role, userId:userId'); // userId is the auth.users.id

    if (error) {
      toast({ title: "Error", description: "Gagal memuat pengguna.", variant: "destructive" });
      setUsers([]);
    } else if (data) {
      // Map Supabase profile data to User type
      const mappedUsers: User[] = data.map(profile => ({
        id: profile.userId, // This is the auth.users.id
        supabaseProfileId: profile.id, // This is the profiles.id (PK of profiles table)
        name: profile.name || '',
        role: profile.role as Role,
        // email and isVerified would require joining with auth.users table or separate queries
        // For now, these might be undefined or fetched on demand for specific actions
      }));
      setUsers(mappedUsers);
    }
    setPageLoading(false);
  }, [toast]);

  useEffect(() => {
    if (currentUser && (currentUser.role === 'admin' || currentUser.role === 'superadmin')) {
      fetchUsers();
    }
  }, [currentUser, fetchUsers]);

  const handleCreateUser = () => {
    setEditingUser(null);
    setIsFormOpen(true);
    toast({ title: "Info", description: "Pembuatan pengguna melalui form belum diimplementasikan sepenuhnya dengan Auth.js. Silakan daftar melalui halaman registrasi."});
  };

  const handleEditUser = (userToEdit: User) => {
    setEditingUser(userToEdit);
    setIsFormOpen(true);
  };

  const handleDeleteUser = async (userId: string) => {
    if (!currentUser || (currentUser.role !== 'admin' && currentUser.role !== 'superadmin')) {
      toast({ title: "Akses Ditolak", description: "Anda tidak memiliki izin untuk menghapus pengguna.", variant: "destructive" });
      return;
    }
    if (userId === currentUser.id) {
        toast({ title: "Tindakan Ditolak", description: "Tidak dapat menghapus pengguna yang sedang login.", variant: "destructive" });
        return;
    }

    setPageLoading(true);
    try {
      const { error: functionError } = await supabase.functions.invoke('delete-user', {
        body: { userIdToDelete: userId },
      });

      if (functionError) {
        throw functionError;
      }
      toast({ title: "Sukses", description: "Pengguna berhasil dihapus." });
      fetchUsers(); // Refresh user list
    } catch (error: any) {
      console.error("Error deleting user:", error);
      const errorMessage = error.context?.errorMessage || error.message || "Gagal menghapus pengguna.";
      toast({ title: "Error", description: errorMessage, variant: "destructive" });
    } finally {
      setPageLoading(false);
    }
  };
  
  const handleVerifySiswa = (userId: string) => {
    // This needs to be implemented via a Supabase function or direct admin update if RLS allows
    toast({ title: "Info", description: "Verifikasi pengguna melalui admin panel belum diimplementasikan sepenuhnya dengan Auth.js/Supabase."});
  };

  const handleChangeRole = async (userToUpdate: User, newRole: Role) => {
    if (!currentUser || (currentUser.role !== 'admin' && currentUser.role !== 'superadmin')) {
        toast({ title: "Akses Ditolak", description: "Anda tidak memiliki izin untuk mengubah peran pengguna.", variant: "destructive" });
        return;
    }
    if (userToUpdate.id === currentUser.id && newRole !== currentUser.role) {
        toast({ title: "Tindakan Ditolak", description: "Tidak dapat mengubah peran akun sendiri melalui antarmuka ini.", variant: "destructive" });
        return;
    }
     if (userToUpdate.role === 'superadmin' && currentUser.role !== 'superadmin') {
      toast({ title: "Akses Ditolak", description: "Hanya Super Admin yang dapat mengubah peran Super Admin lain.", variant: "destructive" });
      return;
    }
    if (newRole === 'superadmin' && currentUser.role !== 'superadmin') {
      toast({ title: "Akses Ditolak", description: "Hanya Super Admin yang dapat menetapkan peran Super Admin.", variant: "destructive" });
      return;
    }


    setPageLoading(true);
    try {
        const { error } = await supabase
            .from('profiles')
            .update({ role: newRole })
            .eq('userId', userToUpdate.id); // use userId (auth.users.id)

        if (error) throw error;
        toast({ title: "Sukses", description: `Peran pengguna ${userToUpdate.name || userToUpdate.id} diubah menjadi ${ROLES[newRole]}.` });
        fetchUsers(); // Refresh list
    } catch (error) {
        toast({ title: "Error", description: "Gagal memperbarui peran pengguna.", variant: "destructive" });
    } finally {
        setPageLoading(false);
    }
  }


  const handleFormSubmit = async (data: any, currentlyEditingUser: User | null) => {
    setPageLoading(true);
    try {
      if (currentlyEditingUser) {
        // Update user's name in profiles table
        const { error } = await supabase
            .from('profiles')
            .update({ name: data.name })
            .eq('userId', currentlyEditingUser.id); // use userId (auth.users.id)
        if (error) throw error;
        toast({ title: "Sukses", description: "Nama pengguna berhasil diperbarui." });
        fetchUsers(); // Refresh list
        setIsFormOpen(false);

        // Password change would require a Supabase Edge function or specific Auth.js handling
        if(data.password){
             toast({ title: "Info", description: "Perubahan kata sandi melalui form ini belum diimplementasikan." });
        }

      } else {
        // User creation through this admin form is complex with Auth.js credentials provider.
        // Users should typically register themselves.
        toast({ title: "Info", description: "Pembuatan pengguna baru sebaiknya dilakukan melalui halaman registrasi standar."});
        setIsFormOpen(false);
      }
    } catch (error) {
      toast({ title: "Operasi Gagal", description: "Tidak dapat menyimpan data pengguna.", variant: "destructive"});
    } finally {
      setPageLoading(false);
    }
  };
  
  if (authLoading) {
    return <div className="flex h-full items-center justify-center"><Loader2 className="h-12 w-12 animate-spin text-primary" /></div>;
  }

  if (!currentUser || (currentUser.role !== 'admin' && currentUser.role !== 'superadmin')) {
    return <p>Akses Ditolak. Anda harus menjadi admin untuk melihat halaman ini.</p>;
  }

  return (
    <div className="space-y-6">
      <div className="flex flex-col md:flex-row items-start md:items-center justify-between gap-4">
        <div>
          <h1 className="text-3xl font-headline font-semibold">Manajemen Pengguna</h1>
          <p className="text-muted-foreground">Lihat, buat, dan kelola akun pengguna dalam sistem.</p>
        </div>
        <Button onClick={handleCreateUser} disabled={true /* User creation via admin form not standard with NextAuth Credentials */}>
          <UserPlus className="mr-2 h-4 w-4" /> Buat Pengguna
        </Button>
      </div>
      
      <Card className="shadow-lg">
        <CardHeader>
          <CardTitle>Semua Pengguna</CardTitle>
          <CardDescription>Daftar semua pengguna dalam sistem. Jumlah pengguna saat ini: {users.length}.</CardDescription>
        </CardHeader>
        <CardContent>
          {(pageLoading || authLoading) && <div className="absolute inset-0 bg-background/50 flex items-center justify-center z-10"><Loader2 className="h-8 w-8 animate-spin text-primary" /></div>}
          <Table>
            <TableHeader>
              <TableRow>
                <TableHead>Nama</TableHead>
                <TableHead>ID Pengguna (Email)</TableHead>
                <TableHead>Peran</TableHead>
                <TableHead>Status Verifikasi</TableHead>
                <TableHead className="text-right">Tindakan</TableHead>
              </TableRow>
            </TableHeader>
            <TableBody>
              {users.map((user) => (
                <TableRow key={user.id}>
                  <TableCell className="font-medium">{user.name || user.id.split('@')[0]}</TableCell>
                  <TableCell>{user.email || user.id}</TableCell>
                  <TableCell>
                    <Badge variant={user.role === 'admin' || user.role === 'superadmin' ? "default" : "secondary"}>
                      {ROLES[user.role]}
                    </Badge>
                  </TableCell>
                  <TableCell>
                    {user.isVerified === undefined ? (
                       <Badge variant="outline">Tidak Diketahui</Badge>
                    ) : user.isVerified ? (
                      <Badge variant="default" className="bg-green-500 hover:bg-green-600 text-white">
                        <CheckCircle className="mr-1 h-3 w-3" /> Terverifikasi
                      </Badge>
                    ) : (
                      <Badge variant="destructive">
                        <ShieldAlert className="mr-1 h-3 w-3" /> Belum Terverifikasi
                      </Badge>
                    )}
                  </TableCell>
                  <TableCell className="text-right">
                    <UserTableActions 
                      user={user} 
                      currentUser={currentUser}
                      onEdit={handleEditUser} 
                      onDelete={handleDeleteUser}
                      onVerify={handleVerifySiswa}
                      onChangeRole={handleChangeRole}
                      isSuperAdmin={currentUser.role === 'superadmin'}
                    />
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </CardContent>
      </Card>
      <UserForm 
        isOpen={isFormOpen} 
        onClose={() => setIsFormOpen(false)} 
        onSubmit={handleFormSubmit}
        editingUser={editingUser}
        isLoading={pageLoading}
      />
    </div>
  );
}
